"use client";

import { createClient } from "@/utils/supabase/client";
import { useRouter } from "next/navigation";
import { useEffect } from "react";

// üí° layout.tsxÎ°úÎ∂ÄÌÑ∞ userIdÎ•º propsÎ°ú Î∞õÏäµÎãàÎã§.
export default function RealtimeProvider({
  userId,
  children,
}: {
  userId: string | undefined;
  children: React.ReactNode;
}) {
  const router = useRouter();
  const supabase = createClient();

  useEffect(() => {
    // üí° Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê(userId)Í∞Ä ÏûàÏùÑ ÎïåÎßå Íµ¨ÎèÖ
    if (userId) {
      const channel = supabase
        .channel(`user-profile-${userId}`)
        .on(
          "postgres_changes",
          {
            event: "UPDATE", // üí° ÌîÑÎ°úÌïÑ "ÏàòÏ†ï"Îßå Í∞êÏßÄ
            schema: "public",
            table: "profiles",
            filter: `id=eq.${userId}`, // üí° "Î°úÍ∑∏Ïù∏Ìïú ÎÇ¥" ÌîÑÎ°úÌïÑÎßå
          },
          (payload) => {
            // üí° ÎÇ¥ ÌîÑÎ°úÌïÑÏù¥ ÏàòÏ†ïÎêòÎ©¥ (Ïòà: /additionalInfo ÌéòÏù¥ÏßÄ)
            // üí° Ìó§ÎçîÎ•º Ìè¨Ìï®Ìïú Î™®Îì† Îç∞Ïù¥ÌÑ∞Î•º ÏÉàÎ°úÍ≥†Ïπ®Ìï©ÎãàÎã§.
            console.log("Profile updated!", payload);
            router.refresh();
          }
        )
        .subscribe();

      return () => {
        supabase.removeChannel(channel);
      };
    }
  }, [supabase, userId, router]);

  // üí° ÏûêÏãù Ïª¥Ìè¨ÎÑåÌä∏Îì§ (Header, main, Footer)ÏùÑ Í∑∏ÎåÄÎ°ú Î†åÎçîÎßÅ
  return <>{children}</>;
}